<template>
  <div class="work">
    <WorkHeader :title="'工作'" />
    <WorkModules />
    <Footer style="z-index:10" />
    <Daily />
    <planModule />
    <!-- <div @click="changeToken" style="text-align: center;line-height: 10vw;">测试</div> -->
  </div>
</template>


<script>
import Vue from 'vue'
import VueRouter from 'vue-router'
import WorkHeader from '../../components/work/header'
import Footer from '../../components/Footer'
import WorkModules from '../../components/work/workModules/'
import Daily from '../../components/work/daily/'
import planModule from '../../components/work/plan/planModule'
import { IndexModel } from "../../utils/"
import { checkLogin } from '../../utils/token/toLogin'
const indexModel = new IndexModel()

export default {
  name: 'work',
  components: {
    WorkHeader,
    Footer,
    WorkModules,
    Daily,
    planModule
  },
  props: [],
  data () {
    return {

    }
  },
  created () {
    checkLogin()
  },
  mounted () {

  },
  methods: {
    changeToken () {
      let token = JSON.parse(localStorage.getItem('token'))
      token.access_token = '1234567891234567891234567'
      token.refresh_token = '2e0ce02f-6076-43d2-b935-5e329faedd0'
      localStorage.setItem('token', JSON.stringify(token))
    }
  }
}
</script>

<style lang="scss" scoped>
.work {
  // box-sizing: border-box;
  // height: 100vh;
  margin-top: 88px;
  background: #f8f8f8;
}
</style>



